export default {
  name: 'TagComponent',
  props: {
    color: {
      type: String,
      default: '#003BB3'
    },
    backgroundColor: {
      type: String,
      default: '#E2ECFF'
    }
  },
  computed: {
    style () {
      return {
        color: this.color,
        backgroundColor: this.backgroundColor
      }
    }
  }
}
